.Recent {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding-inline: 3rem;
  height: 8.6rem;
}

@media (max-width: 430px) {
  .Recent {
    margin-right: 1rem;
  }
}

.titulo {
  font-family: 'Merriweather Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.5rem;
  /* identical to box height */

  color: var(--text-color);
  white-space: nowrap;
}